@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html.no-scroll, html.no-scroll body {
  overflow: hidden;
  height: 100%;
}
html.no-scroll-long {
  overflow-y: scroll;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

p {
  color: #000000;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.margin {
  margin: 2rem 0;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.ocultar {
  display: none;
}

.w-100 {
  width: 100%;
}

.centrar-contenido {
  max-width: 55rem;
  margin: 0 auto;
}

