/** Media Queries **/
.boton-celeste-block {
  background-color: #5fa2e6;
  color: #ffffff;
  padding: 1rem 3rem;
  font-weight: 600;
  text-align: center;
  display: block;
  margin-top: 1rem;
  transition: 200ms;
  border: none;
  cursor: pointer;
  min-height: 5rem;
}
.boton-celeste-block:hover {
  background-color: #4995e3;
}

.boton-celeste {
  background-color: #5fa2e6;
  color: #ffffff;
  padding: 1rem 3rem;
  font-weight: 600;
  text-align: center;
  display: inline-block;
  margin-top: 1rem;
  transition: 200ms;
  border: none;
  cursor: pointer;
  min-height: 5rem;
}
.boton-celeste:hover {
  background-color: #4995e3;
}

.boton-verde {
  background-color: #71b100;
  color: #ffffff;
  padding: 1rem 3rem;
  font-weight: 600;
  text-align: center;
  display: inline-block;
  margin-top: 2.5rem;
  transition: 200ms;
  border: none;
  cursor: pointer;
  min-height: 5rem;
}
.boton-verde:hover {
  background-color: #619800;
}

.boton-verde-block {
  background-color: #71b100;
  color: #ffffff;
  padding: 1rem 3rem;
  font-weight: 600;
  text-align: center;
  display: block;
  margin-top: 2.5rem;
  transition: 200ms;
  border: none;
  cursor: pointer;
  min-height: 5rem;
}
.boton-verde-block:hover {
  background-color: #619800;
}

.boton-rojo-block {
  background-color: #a01717;
  color: #ffffff;
  padding: 1rem 3rem;
  font-weight: 600;
  text-align: center;
  display: block;
  margin-top: 2rem;
  transition: 200ms;
  border: none;
  cursor: pointer;
  min-height: 5rem;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #8a1414;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #444444;
  padding: 2.5rem 0;
}
.header.inicio {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 70rem;
  max-height: 100vh;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
}
.contenido-header h1 {
  text-align: left;
  color: #ffffff;
  font-weight: 700;
  line-height: 2;
  font-size: 2.6rem;
}
@media (min-width: 290px) {
  .contenido-header h1 {
    width: 80%;
    font-size: 3rem;
  }
}
@media (min-width: 1080px) {
  .contenido-header h1 {
    width: 60%;
    font-size: 3.8rem;
  }
}

.barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    width: 100%;
  }
}
.barra .logo {
  max-width: 60%;
  z-index: 1;
}
.barra .logo img {
  height: 5rem;
}
@media (min-width: 768px) {
  .barra .logo {
    max-width: 35%;
  }
}
@media (min-width: 768px) {
  .barra .logo {
    width: auto;
  }
}

.navegacion-desktop {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  height: 0vh;
  transition: 0ms;
  overflow: hidden;
  flex-direction: column;
  width: 100%;
  transition: all 800ms ease;
  background-color: #000000;
}
.navegacion-desktop.activo {
  height: 100vh;
}
.navegacion-desktop.activo a {
  opacity: 0.8;
  transform: translateY(0);
}
@media (min-width: 768px) {
  .navegacion-desktop {
    background-color: transparent;
    transition: 0ms;
    position: initial;
    height: auto;
    flex-direction: row;
    width: auto;
  }
}

.navegacion-desktop a {
  color: #ffffff;
  opacity: 0;
  padding: 2rem 0;
  border-bottom: 0.1rem solid #ffffff;
  width: 85%;
  margin: 0 auto;
  transition: all 1200ms ease;
  transform: translateY(-10rem);
}
.navegacion-desktop a:hover {
  color: #5fa2e6;
  transition: 300ms;
}
.navegacion-desktop a:first-of-type {
  margin-top: 10rem;
}
.navegacion-desktop a:last-of-type {
  border-bottom: none;
}
@media (min-width: 768px) {
  .navegacion-desktop a {
    margin-right: 2rem;
    opacity: 1;
    border: none;
    transform: translateY(0px);
    transition: 0ms;
    padding: 0;
    width: auto;
  }
  .navegacion-desktop a:hover {
    transition: 300ms;
  }
  .navegacion-desktop a:first-of-type {
    margin-top: 0;
  }
  .navegacion-desktop a:last-of-type {
    margin-right: 0;
  }
}

.navegacion-mobile {
  width: 2.5rem;
  height: 2.2rem;
  margin-right: 1.5rem;
  z-index: 1;
}
@media (min-width: 768px) {
  .navegacion-mobile {
    display: none;
  }
}

.icono-nav {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.icono-nav .linea-1,
.icono-nav .linea-2,
.icono-nav .linea-3 {
  height: 0.3rem;
  width: 100%;
  position: absolute;
  background-color: #ffffff;
  border-radius: 2rem;
  transition: 500ms;
  right: 0;
}
.icono-nav .linea-1 {
  top: 0.2rem;
  width: 60%;
}
.icono-nav .linea-2 {
  top: 0.95rem;
}
.icono-nav .linea-3 {
  top: 1.7rem;
}

.navegacion-mobile.activo .linea-1 {
  width: 100%;
}
.navegacion-mobile.activo .linea-2 {
  right: 0;
  width: 60%;
}
.navegacion-mobile.activo .linea-3 {
  right: 0;
  width: 60%;
}

.footer {
  background-color: #444444;
  padding: 3rem 0;
  margin-bottom: 0;
}

.contenido-footer {
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-footer {
    display: flex;
    justify-content: space-between;
  }
}
.contenido-footer p {
  color: #ffffff;
}
.contenido-footer .navegacion-desktop {
  display: none;
}
@media (min-width: 768px) {
  .contenido-footer .navegacion-desktop {
    display: block;
  }
}

.contenedor-nosotros {
  margin-top: 2.5rem;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .contenedor-nosotros {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.iconos {
  text-align: center;
}
.iconos img {
  height: 10rem;
}

@media (min-width: 768px) {
  .inferior-anuncio .boton-verde {
    margin-right: 5vw;
  }
}
@media (min-width: 1080px) {
  .inferior-anuncio .boton-verde {
    margin-right: 0;
  }
}

.contenedor-anuncios {
  position: relative;
  margin-top: 2.5rem;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
    padding: 0 5vw;
  }
}
@media (min-width: 1080px) {
  .contenedor-anuncios {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
    padding: 0;
  }
}

.anuncio {
  background-color: #eeeeee;
  border: 0.1rem solid #c8c8c8;
  display: none;
}
.anuncio.ocultar {
  display: none;
}
.anuncio.display {
  display: grid;
}
.anuncio.mover {
  grid-column: 1/2;
}
.anuncio.acomodar {
  grid-row: 1/2;
  grid-column: 2/3;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3 {
  font-size: 2.5rem;
}
.anuncio h3,
.anuncio p {
  margin-bottom: 1rem;
}
.anuncio p {
  text-align: justify;
}

.precio {
  color: #507e00;
  font-weight: 700;
}

.iconos-anuncio {
  list-style: none;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
}
.iconos-anuncio li {
  display: flex;
  align-items: center;
  width: 80%;
}
.iconos-anuncio li img {
  height: 3rem;
}

.inferior-anuncio {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .inferior-anuncio {
    gap: 3rem;
  }
}

.flechas {
  display: flex;
  margin-top: 2.5rem;
  justify-content: center;
}
.flechas .flecha-izq,
.flechas .flecha-der {
  width: 2rem;
  cursor: pointer;
  opacity: 0.6;
  transition: 0.3s;
  margin: 0 3rem;
  border-radius: 50%;
}
.flechas .flecha-izq:hover,
.flechas .flecha-der:hover {
  opacity: 1;
}
@media (min-width: 768px) {
  .flechas .flecha-izq,
.flechas .flecha-der {
    display: none;
  }
}

.flecha-der.tablet,
.flecha-izq.tablet {
  display: none;
}
@media (min-width: 768px) {
  .flecha-der.tablet,
.flecha-izq.tablet {
    display: block;
    position: absolute;
    width: 3rem;
    height: 100%;
    opacity: 0.6;
    cursor: pointer;
  }
  .flecha-der.tablet:hover,
.flecha-izq.tablet:hover {
    opacity: 1;
  }
}
@media (min-width: 1080px) {
  .flecha-der.tablet,
.flecha-izq.tablet {
    display: none;
  }
}

.flecha-der.tablet {
  right: 0;
}

.descripcion-anuncio {
  max-height: 15rem;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 12rem 2rem;
}

.contenido-contacto {
  text-align: center;
}
.contenido-contacto h2,
.contenido-contacto p {
  color: #ffffff;
  margin: 1rem 0;
}
.contenido-contacto h2 {
  font-weight: 600;
}

@media (min-width: 1080px) {
  .seccion-final {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 3rem;
  }
}

.entrada-blog {
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 2rem;
  }
}
@media (min-width: 1080px) {
  .entrada-blog:last-of-type {
    margin-bottom: 0;
  }
}
.entrada-blog .texto-entrada {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.entrada-blog .texto-entrada p {
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .entrada-blog .texto-entrada p {
    text-align: start;
  }
}
.entrada-blog .texto-entrada a {
  color: #000000;
}
.entrada-blog .texto-entrada a ::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71b100;
  margin: 1rem auto 0;
}
@media (min-width: 768px) {
  .entrada-blog .texto-entrada a ::after {
    margin-left: 0;
  }
}

.heading-entrada {
  font-size: 2.5rem;
  margin: 1rem 0;
  line-height: 1.2;
}
@media (min-width: 768px) {
  .heading-entrada {
    text-align: start;
    margin-top: 0;
  }
}

.texto-meta span {
  color: #5fa2e6;
  font-weight: 600;
}

@media (min-width: 768px) {
  .testimoniales {
    width: 60%;
    margin: 0 auto;
  }
}
@media (min-width: 1080px) {
  .testimoniales {
    width: 100%;
  }
}

.testimonial {
  background-color: #71b100;
  border-radius: 2.5rem;
  padding: 2rem;
  font-size: 1.8rem;
  margin-top: 2rem;
  color: #ffffff;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  display: block;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  height: 5rem;
  width: 5rem;
  position: absolute;
  left: -0.5rem;
}
.testimonial p {
  margin-top: 1rem;
  color: #ffffff;
  text-align: right;
}

.contenido-nosotros {
  margin-top: 2rem;
}
@media (min-width: 1080px) {
  .contenido-nosotros {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros {
  margin-top: 2rem;
}
@media (min-width: 1080px) {
  .texto-nosotros {
    margin-top: 0;
  }
}
.texto-nosotros blockquote,
.texto-nosotros p {
  text-align: left;
}
.texto-nosotros blockquote {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.pagina-anuncios .contenedor-anuncios {
  position: initial;
  padding: 0;
}
.pagina-anuncios .contenedor-anuncios .anuncio {
  display: grid;
}
.pagina-anuncios .contenedor-anuncios .anuncio .imagen-propiedad {
  max-height: 25rem;
  object-fit: cover;
}

.informacion-anuncio {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem auto 3rem;
}
@media (min-width: 1080px) {
  .informacion-anuncio {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 5rem;
    max-width: 60%;
    margin-bottom: 2rem;
  }
}
.informacion-anuncio p {
  display: flex;
  align-items: center;
}
.informacion-anuncio .iconos-anuncio {
  gap: 3rem;
}
.informacion-anuncio .iconos-anuncio p {
  margin-left: 1rem;
}
@media (min-width: 1080px) {
  .informacion-anuncio .iconos-anuncio p {
    margin-left: 0;
  }
}
.informacion-anuncio .precio {
  text-align: center;
  font-size: 3.5rem;
  margin-left: 1rem;
}

.formulario legend {
  font-size: 2rem;
}

fieldset {
  margin-bottom: 1rem;
  border: 1px solid #444444;
}
fieldset div {
  display: flex;
  flex-direction: column;
}
fieldset:last-of-type {
  margin-bottom: 0;
}
fieldset label {
  font-weight: 600;
}
fieldset label,
fieldset p {
  margin: 0.5rem 0;
}
fieldset input,
fieldset select,
fieldset textarea {
  padding: 1rem;
  width: 100%;
}
fieldset textarea {
  height: 25rem;
  max-height: 40rem;
  width: 100%;
}

@media (min-width: 768px) {
  .informacion-personal {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3rem;
  }
  .informacion-personal div:nth-child(2) {
    grid-column: 1/3;
  }
  .informacion-personal div:nth-child(3) {
    grid-column: 1/2;
  }
  .informacion-personal div:nth-child(4) {
    grid-column: 2/3;
  }
  .informacion-personal div:nth-child(5) {
    grid-column: 1/3;
  }
}

@media (min-width: 768px) {
  .informacion-propiedad {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.informacion-contacto .forma-contacto {
  flex-direction: row;
  align-items: center;
}
.informacion-contacto .forma-contacto input {
  margin: 0 3rem 0 1rem;
  width: auto;
}
@media (min-width: 768px) {
  .informacion-contacto .fecha-hora {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #ffffff;
  font-weight: 600;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #a01717;
}
.alerta.correcto {
  background-color: #71b100;
}

table.propiedades {
  margin-top: 3rem;
  width: 100%;
  text-align: center;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71b100;
}
table.propiedades thead th {
  color: #ffffff;
  padding: 1rem;
}
table.propiedades td {
  padding: 0 0.5rem;
}
table.propiedades .admin-mobile {
  display: none;
}
@media (min-width: 1080px) {
  table.propiedades .admin-mobile {
    display: revert;
  }
}
table.propiedades .imagen-tabla {
  width: 15rem;
  margin: 0 auto;
  max-height: 12rem;
  object-fit: cover;
}

.imagen-small {
  max-width: 100%;
}
@media (min-width: 480px) {
  .imagen-small {
    width: 35rem;
  }
}